import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  Modal,
  Form,
  Input,
  message
} from 'antd';
import {
  authError
} from '@/actions';
import goodService from '@/services/goodService';
import CategorySelector from '../../components/CategorySelector';

const FormItem = Form.Item

@connect(
  state => ({
    adminId: state.auth.admin.adminId,
    token: state.auth.admin.token
  }),
  dispatch => ({
    authError: (errorMessage) => dispatch(authError(errorMessage))
  })
)
@Form.create()
export default class UpdateGoodModal extends React.Component {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    handleSubmit: PropTypes.func.isRequired,
    handleCancel: PropTypes.func.isRequired,
    adminId: PropTypes.number.isRequired,
    token: PropTypes.string.isRequired,
    authError: PropTypes.func.isRequired
  }

  state = {
    imageChanged: false
  }

  handleCancel = () => {
    this.props.form.resetFields()
    this.props.handleCancel()
  }

  handleUpdate = (e) => {
    e.preventDefault()

    this.props.form.validateFields((err, values) => {
      console.log(values)
      if (err) {
        return ;
      }

      this.updateGood(values)
    })
  }

  updateGood = async (good) => {
    const {
      adminId,
      token,
      handleSubmit,
      authError
    } = this.props

    try {
      const res = await goodService.update(
        adminId,
        token,
        good
      )
      message.success("")
      handleSubmit()
      this.props.form.resetFields()
    } catch (err) {
      if (err.response === undefined) {
        const errorMessage = ''
        authError(errorMessage)
      }
      if (err.response.status === 401) {
        const errorMessage = ''
        authError(errorMessage)
      }
      if (err.response.status === 400 || err.response.status === 404) {
        const errorMessage = err.response.data.message
        message.error(errorMessage)
      }
    }
  }

  render() {
    const {
      visible,
      handleCancel,
      form
    } = this.props

    const { getFieldDecorator } = form
    console.log(this.props.updateForm)
    let updateForm = this.props.updateForm || {}

    return (
      <Modal
        visible={visible}
        title=""
        okText=""
        cancelText=""
        onCancel={this.handleCancel}
        onOk={this.handleUpdate}
      >
        <Form layout="vertical">
          <FormItem label="id:">
            {getFieldDecorator('goodId', {
              initialValue: updateForm.goodId || ''
            })(
              <Input disabled />
            )}
          </FormItem>
          <FormItem label=":">
            {getFieldDecorator('goodName', {
              initialValue: updateForm.goodName || '',
              rules: [{
                isRequired: true,
                message: ''
              }, {
                max: 20,
                min: 1,
                message: '20'
              }]
            })(
              <Input type="text" />
            )}
          </FormItem>
          <FormItem label="">
            {getFieldDecorator('categorySecondId', {
              initialValue: updateForm.categorySecondId || '',
              rules: [{
                required: true,
                message: ''
              }]
            })(
              <CategorySelector />
            )}
          </FormItem>
          <FormItem label=":">
            {getFieldDecorator('price', {
              initialValue: '' + updateForm.price || '',
              rules: [{
                isRequired: true,
                message: ''
              }, {
                max: 10,
                min: 1,
                message: '10'
              }]
            })(
              <Input type="number"/>
            )}
          </FormItem>
          <FormItem label=":">
            {getFieldDecorator('originalPrice', {
              initialValue: '' + updateForm.originalPrice || '',
              rules: [{
                isRequired: true,
                message: ''
              }, {
                max: 10,
                min: 1,
                message: '10'
              }]
            })(
              <Input type="number"/>
            )}
          </FormItem>
          <FormItem label=":">
            {getFieldDecorator('spec', {
              initialValue: updateForm.spec || '',
              rules: [{
                isRequired: true,
                message: ''
              }]
            })(
              <Input />
            )}
          </FormItem>
          <FormItem label=":">
            {getFieldDecorator('origin', {
              initialValue: updateForm.origin || '',
              rules: [{
                isRequired: true,
                message: ''
              }]
            })(
              <Input />
            )}
          </FormItem>
        </Form>
      </Modal>
    )
  }
}



