import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  Button,
  message,
  Modal,
  Form,
  Radio,
  Input
} from 'antd';
import {
  authError,
  fetchAdminList
} from '@/actions/index';
import adminInfoService from '@/services/adminInfoService';

const FormItem = Form.Item
const RadioGroup = Radio.Group

@connect(
  state => ({
    adminId: state.auth.admin.adminId,
    token: state.auth.admin.token
  }),
  dispatch => ({
    authError: (errorMessage) => authError(errorMessage),
    fetchAdmins: (adminId, token) => dispatch(fetchAdminList(adminId, token))
  })
)
@Form.create()
export default class UpdateAdminModal extends React.Component {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    handleCancel: PropTypes.func.isRequired,
    handleSubmit: PropTypes.func.isRequired,
    value: PropTypes.object.isRequired
  }

  handleSubmit = (e) => {
    e.preventDefault()

    this.props.form.validateFields((err, values) => {
      console.log(values)
      if (err) {
        return ;
      }

      this.updateCategory(values)
    })
  }

  updateCategory = async (admin) => {
    const {
      adminId,
      token
    } = this.props

    try {
      const res = await adminInfoService.update(
        adminId,
        token,
        admin
      )
      message.success('')
      this.props.fetchAdmins(adminId, token)
      this.props.handleSubmit()
    } catch (err) {
      if (err.message === undefined) {
        const errorMessage = ''
        this.props.authError(errorMessage)
      }
      if (err.response.status === 401) {
        const errorMessage = ''
        this.props.authError(errorMessage)
      }
      // 
      if (err.response.status === 400 ||err.response.status === 404) {
        const errorMessage = err.response.data.message
        message.error(errorMessage)
      }
    }
  }

  render() {
    const {
      visible,
      handleCancel,
      handleSubmit,
      form,
      value
    } = this.props

    const { getFieldDecorator } = form
    const administratorId = value ? value.administratorId: ''
    const passWord = value ? value.passWord : ''
    const nickName = value ? value.nickName : ''
    const phone = value ? value.phone : ''
    const superLevel = value ? value.superLevel : ''

    return (
      <Modal
        visible={visible}
        title=""
        okText=""
        cancelText=""
        onCancel={handleCancel}
        onOk={this.handleSubmit}
      >
        <Form layout="vertical">
          <FormItem label="id">
            {getFieldDecorator('administratorId', {
              require: [{
                required: true,
                message: 'id'
              }],
              initialValue: administratorId
            })(
              <Input type="text" disabled/>
            )

            }
          </FormItem>
          <FormItem label="">
            {getFieldDecorator('passWord', {
              rules: [{
                required: true,
                message: ''
              }, {
                max: 30,
                min: 1,
                message: '30'
              }],
              initialValue: passWord
            })(
              <Input type="text" />
            )}
          </FormItem>
          <FormItem label="">
            {getFieldDecorator('nickName', {
              rules: [{
                required: true,
                message: ''
              }, {
                max: 20,
                min: 1,
                message: '20'
              }],
              initialValue: nickName
            })(
              <Input type="text" />
            )}
          </FormItem>
          <FormItem label="">
            {getFieldDecorator('phone', {
              rules: [{
                required: true,
                message: ''
              }],
              initialValue: phone
            })(
              <Input type="number" />
            )}
          </FormItem>
          <FormItem label="">
            {getFieldDecorator('superLevel', {
              rules: [{
                required: true,
                message: ''
              }],
              initialValue: superLevel
            })(
              <RadioGroup>
                <Radio value={true}></Radio>
                <Radio value={false}></Radio>
              </RadioGroup>
            )}
          </FormItem>
        </Form>
      </Modal>
    )
  }
}



