import React from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {
  Button,
  message,
  Modal,
  Form,
  Input
} from 'antd';
import {
  authError,
  fetchUsers
} from '../../actions/index';

import userService from '../../services/userService';


const FormItem = Form.Item


@connect(
  state => ({
    adminId: state.auth.admin.adminId,
    token: state.auth.admin.token
  }),
  dispatch => ({
    authError: (errorMessage) => dispatch(authError(errorMessage)),
    fetchUsers: (adminId,token) => dispatch(fetchUsers(adminId,token))
  })
)

@Form.create()

export default class UpdatePassWord extends React.Component {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    handleCancel: PropTypes.func.isRequired,
    handleSubmit: PropTypes.func.isRequired,
    value: PropTypes.object.isRequired
  }

  // 
  handleSubmit = (e) => {
    e.preventDefault()

    this.props.form.validateFields((err, values) => {
      console.log(values)
      if (err) {
        return;
      }

      // 
      this.updateUser(values);

    })
  }

  updateUser = async (user) => {
    const {
      adminId,
      token
    } = this.props

    try {
      // api
      const res = await userService.update(
        adminId,
        token,
        user
      )
      message.success('');
      this.props.fetchUsers(adminId,token);
      this.props.handleSubmit();
    } catch (err) {
      if (err.message === undefined) {
        const errorMessage = ''
        this.props.authError(errorMessage)
      }
      if (!err.response) {
        this.props.authError(err);
      }
      if (err.response.status === 401) {
        const errorMessage = ''
        this.props.authError(errorMessage)
      }
      // 
      if (err.response.status === 400 || err.response.status === 404) {
        const errorMessage = err.response.data.message
        message.error(errorMessage)
      }
    }
  }

  render() {
    const {
      visible,
      handleCancel,
      form,
      value
    } = this.props

    const {getFieldDecorator} = form;//
    const userId = value ? value.userId : ''
    const userName = value ? value.userName : ''
    const passWord = value ? value.passWord : ''

    return (
      <Modal
        visible={visible}
        title=""
        okText=""
        cancelText=""
        onCancel={handleCancel}
        onOk={this.handleSubmit}
      >
        <Form layout="vertical">
          <FormItem label="id">
            {getFieldDecorator('userId', {
              initialValue: userId
            })(
              <Input type="text" disabled/>
            )}
          </FormItem>
          <FormItem label="">
            {getFieldDecorator('userName', {
              initialValue: userName
            })(
              <Input type="text" disabled/>
            )}
          </FormItem>
          <FormItem label="">
            {getFieldDecorator('passWord', {
              rules: [{
                required: true,
                message: ''
              }, {
                max: 30,
                min: 1,
                message: '30'
              }],
              initialValue: passWord
            })(
              <Input type="text"/>
            )}
          </FormItem>
        </Form>
      </Modal>
    )
  }

}



